import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import M from "materialize-css"
const Signup = () => {
const navigate=useNavigate()
const [name,setName]=useState("")
const [email,setEmail]=useState("")
const [password,setPassword]=useState("")
const [image,setImage]=useState("")
const [url,setUrl]=useState(undefined)



useEffect(()=>{
  if(url){
    uploadFields()
  }
},[url])


const uploadPic=()=>{
  const data = new FormData();
    data.append("file", image);
    data.append("upload_preset", "insta-clone");
    data.append("cloud_name", "dhcnacghe");
    fetch("https://api.cloudinary.com/v1_1/dhcnacghe/image/upload", {
      method: "post",
      body: data,
    })
      .then((res) => res.json())
      .then((data) => {
        setUrl(data.url);
      })
      .catch((err) => {
        console.log(err);
      });
}


const uploadFields=()=>{
  if(!/^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(email)){
    M.toast({html: "invalid email",classes:"#c62828 red darken-3"})
    return
  }
  fetch("/signup",{
    method:"post",
    headers:{
      "Content-Type":"application/json"
    },
    body:JSON.stringify({
      name,
      password,
      email,
      pic:url
    })
  }).then((res)=>res.json())
  .then((data)=>{
    if(data.error){
      M.toast({html: data.error,classes:"#c62828 red darken-3"})
    }
    else{
      M.toast({html: data.message,classes:"#76ff03 light-green accent-3"})
     navigate("/login")
    }
  })
  .catch((error)=>{
    console.log(error)
  })
}

const PostData=()=>{

if(image){
  uploadPic()
}else{
  uploadFields()
}

  
}

  return (
    <div className="mycard">
    <div className="card auth-card input-field">
      <h2>Instagram</h2>
      <input type="text" placeholder="name" value={name} onChange={(e)=>{
        setName(e.target.value)
      }} />
      <input type="text" placeholder="@email"  value={email} onChange={(e)=>{
        setEmail(e.target.value)
      }}/>

      <input type="password" placeholder="password" value={password} onChange={(e)=>{
        setPassword(e.target.value)
      }}/>



<div className="file-field input-field">
        <div className="btn #64b5f6 blue darken-2">
          <span>Upload pic</span>
          <input type="file" onChange={(e) => setImage(e.target.files[0])} />
        </div>
        <div className="file-path-wrapper">
          <input
            className="file-path validate"
            type="text"
            placeholder="Upload one or more files"
          />
        </div>
      </div>


      <button className="btn waves-effect waves-light #64b5f6 blue darken-2" onClick={()=>PostData()}>
      SignUP
      </button>
      <h5>
        <Link to="/login">Already have an account ?</Link>
      </h5>
    </div>
  </div>
  )
}

export default Signup