
import React,{useContext} from 'react'
import { Link  ,useNavigate} from 'react-router-dom'
import { usercontext } from '../App'
import CameraAltIcon from '@mui/icons-material/CameraAlt';
import PersonIcon from '@mui/icons-material/AccountCircle';
import Tooltip from '@mui/material/Tooltip';
const Navbar = () => {
  const {state,dispatch}=useContext(usercontext)
  const  navigate=useNavigate()
  const renderList=()=>{
    if(state){
      return (
        <>
        <li><Link to="/profile">   <Tooltip title="Profile">
        <PersonIcon className='create-post'>
        </PersonIcon>
        </Tooltip></Link>    </li>



        <li><Link to="/CreatePost">
        
        <Tooltip title="Create post">
        <CameraAltIcon className='create-post'>
        </CameraAltIcon>
        </Tooltip>
        </Link></li>
   
        <li>
        <button className="btn waves-effect waves-light #ff1744 red accent-3"   onClick={() => {localStorage.clear()
         dispatch({type:"CLEAR"})
         navigate("/login")
          }}>LOGOUT
   
   
  </button>
        </li>
        </>
      )
    }else{
      return(
        <>
        <li><Link to="/login">Login</Link></li>
        <li><Link to="/signup">Signup</Link></li>
        </>
      )
    }
  }
  return (
    <nav>
    <div className="nav-wrapper white" >
      <Link to={state ? "/":"/login"} className="brand-logo left">Instagram</Link>
      <ul id="nav-mobile" className="right ">
        {renderList()}
       
      </ul>
    </div>
  </nav>
        
  )
}

export default Navbar